.skills-container{
    margin: 4rem 0rem;
    position: relative;
}

.skills-container::after,
.skills-container::before {
    content: " ";
    width: 28rem;
    height: 28rem;
    border-radius: 28.125rem;
    background-color: rgb(80, 89, 168);
    position: absolute;
    z-index: -1;
    filter: blur(200px);
}

.skills-container::after {
    top: -3rem;
    left: -5rem;
}
.skills-container::before {
    background: rgb(92, 49, 112);
    bottom: -7rem;
    right: -5rem;
}

.skills-container h5{
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3.5rem;
}

.skills-content{
    display: flex;
    align-items: flex-start;
    gap: 3rem;

}

.skills {
    /* height: 5rem;
    width: 3rem; */
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
}

.skills-info{
    flex: 1;
}

@media (max-width: 1025px) {
    .skills {
        padding-left: 1rem;
        width: 100%;
    }

    /* .skills,
    .skills-content {
        grid-gap: 2rem;
    }

    .skills-info{
        width: 100%;
    } */
}

@media (max-width: 799px) {
    .skills-content{
        display: flex;
        flex-direction: column;
        gap: 3rem;
    }

    .skills, .skills-info {
        width: 100%;
    }

    .skills-container h5{
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }

    .skills-container::after,
    .skills-container::before {
        width: 18rem;
        height: 18rem;
    }
}

@media (max-width: 600px) {
    .skills-container {
        padding: 0;
    }
}