.skills-card{
    border-radius: 0.65rem;
    border: 3px solid rgb(168, 86, 234);
    padding: 2.5rem;
    text-align: center;
    background: transparent;
    backdrop-filter: blur(1rem);
    position: relative;
    cursor: pointer;
    transition: all 0.9s ease;
}

.skills-card:hover,
.skills-card.active {
    background: linear-gradient(180deg, rgb(166, 1, 255) 0%, rgb(87, 0, 128) 100%);
}

.skills-card span{
    font-size: 1.3rem;
    font-weight: 500;
}

.skill-icon{
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(6, 6, 44);
    border-radius: 0.65rem;
    border: 3px solid rgb(168, 86, 234);
    position: absolute;
    top: -1rem;
    left: -1rem;
}

.skill-icon img{
    width: 2.4rem;
    height: auto;
    object-fit: contain;
}

@media (max-width: 1025px) {
    .skills-card {
        padding: 2rem;
    }
    .skill-icon span{
        font-size: 1rem;
    }
    .skill-icon{
        width: 3rem;
        height: 3rem;
    }
    .skill-icon img{
        width: 1.8rem;
    }
}

@media (max-width: 768px){
    .skills-card span {
        font-size: 0.9rem;
    }

}